.details {
  @include reset-list;

  &__link {
    @include reset-link;
    padding: 15px 0;
    color: var(--white);
    transition: opacity 0.25s ease-in-out, outline 0.25s ease-in-out;

    &:hover {
      opacity: 0.75;
    }

    &:focus-visible {
      outline: 1px solid var(--white);
    }
  }
}