.slider {
  @include reset-list;
  @include flex-b-s;
  gap: 30px;

  &__item {
    &:nth-child(3) {
      display: none;

      @include media($wide-mobile) {
        display: initial;
      }

      @include media($tablet) {
        display: none;
      }

      @include media($wide-tablet) {
        display: initial;
      }
    }

    &:nth-child(4) {
      display: none;

      @include media($desktop) {
        display: initial;
      }
    }
  }

  &__link {
    @include reset-link;
    transition: outline 0.25s ease-in-out;

    &:hover .slider__title {
      text-shadow: var(--text-d);
    }

    &:focus {
      outline: 1px solid var(--muesli-i);
      outline-offset: 3px;
    }
  }

  &__article {
    text-align: center;
  }

  &__figure {
    @include reset-figure;
    margin-bottom: 15px;
  }

  &__image {
    margin-bottom: 20px;

    @include media($tablet) {
      width: 324px;
      height: 340px;
      margin-bottom: 25px;
    }

    @include media($desktop) {
      width: 270px;
      height: 284px;
    }
  }

  &__title {
    margin: 0;
    font-size: inherit;
    font-weight: inherit;
    max-width: 130px;
    transition: text-shadow 0.25s ease-in-out;

    @include media($tablet) {
      max-width: 324px;
    }
  }

  &__price {
    font-size: 14px;
  }
}
