.search {
  position: relative;

  &__input {
    @include reset-input;
    @include description-font;
    min-width: 100%;
    padding: 12px 12px 12px 40px;
    background-color: var(--search-bg);
    border-radius: 21px;
    transition: box-shadow 0.25s ease-in-out, outline 0.25s ease-in-out;

    &:hover {
      box-shadow: 3px 2px 1px 0 var(--muesli-i);
    }

    &:focus-visible {
      box-shadow: 3px 2px 1px 0 var(--muesli-i);
      outline: 1px solid var(--muesli-i);
      outline-offset: 3px;
    }
  }

  &__button {
    @include reset-button;
    @include flex-all-c;
    width: 16px;
    height: 16px;
    position: absolute;
    top: 50%;
    left: 12px;
    transform: translateY(-50%);
    color: var(--white);

    @include media($desktop) {
      color: var(--zeus);
    }

    &:hover {
      color: var(--muesli-i);
    }

    &:focus-visible {
      color: var(--muesli-i);
      outline: 1px solid var(--muesli-i);
      outline-offset: 3px;
    }
  }
}