@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Poppins-Medium.woff2") format("woff2"), url("../fonts/Poppins-Medium.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Poppins-Regular.woff2") format("woff2"), url("../fonts/Poppins-Regular.woff") format("woff");
}
@font-face {
  font-family: "Garamond";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/CormorantGaramond-SemiBold.woff2") format("woff2"), url("../fonts/CormorantGaramond-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Garamond";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/CormorantGaramond-Bold.woff2") format("woff2"), url("../fonts/CormorantGaramond-Bold.woff") format("woff");
}
@font-face {
  font-family: "Garamond";
  font-style: italic;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/CormorantGaramond-Italic.woff2") format("woff2"), url("../fonts/CormorantGaramond-Italic.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

img {
  display: block;
  max-width: 100%;
  object-fit: contain;
}

svg {
  display: block;
  height: 100%;
  width: 100%;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  font-family: var(--head-f);
  font-weight: var(--head-f-w);
  font-size: var(--head-f-s);
  font-style: var(--head-f-style);
  line-height: var(--head-l-h);
}

:root {
  --white: #ffffff;
  --zeus: #1d1613;
  --muesli: #a87b62;
  --muesli-i: #7a5340;
  --dawn: #9b9997;
  --desert-storm: #f6f5f4;
  --judge-gray: #564034;
  --search-bg: rgba(255, 255, 255, 0.15);
  --hero-bg: rgba(86, 64, 52, 0.7);
  --article-bg: #f0eae4;
  --head-f: "Poppins", "Arial", sans-serif;
  --head-f-w: 500;
  --head-f-s: 16px;
  --head-l-h: 130%;
  --head-f-style: normal;
  --text-d: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.link {
  display: block;
  color: inherit;
  text-decoration: none;
  position: relative;
  transition: text-shadow 0.25s ease-in-out, outline 0.25s ease-in-out;
}
.link:focus-visible {
  outline: 1px solid var(--muesli);
  outline-offset: 2px;
}
.link::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -10px;
  width: 100%;
  height: 1px;
  background-color: var(--muesli);
  transition: width 0.25s ease-in-out;
}
.link:hover {
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.link:hover::after {
  width: 0;
}

.title {
  font-family: "Garamond";
  font-style: normal;
  font-weight: 700;
  font-size: 34px;
  margin-top: 0;
}

.page {
  display: flex;
  flex-flow: column nowrap;
}

.header {
  background-color: var(--white);
}
.header--active {
  background-color: var(--muesli);
  overflow: hidden;
}
@media (min-width: 1360px) {
  .header {
    background-color: var(--white);
  }
}
.header__container {
  position: relative;
  padding: 20px 15px;
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 45px;
}
@media (min-width: 767px) {
  .header__container {
    row-gap: 55px;
    padding: 30px 45px;
  }
}
.header__container--active {
  padding-bottom: 100px;
}
@media (min-width: 767px) {
  .header__container--active {
    padding-bottom: 130px;
  }
}
@media (min-width: 1360px) {
  .header__container {
    row-gap: 35px;
    padding: 30px 95px;
    padding-bottom: 0;
  }
}
.header__menu {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  justify-content: space-between;
  row-gap: 30px;
}
@media (min-width: 767px) {
  .header__menu {
    row-gap: 25px;
  }
}
@media (min-width: 1360px) {
  .header__menu {
    row-gap: 0;
    column-gap: 350px;
    grid-template-columns: 1fr 66px 1fr;
  }
}
.header__search {
  display: none;
  grid-column: 1/-1;
}
.header__search--active {
  display: initial;
  color: var(--white);
  grid-row: 2/3;
}
.header__search--active > input::placeholder {
  visibility: hidden;
}
@media (min-width: 1360px) {
  .header__search {
    display: initial;
    grid-row: 1/2;
    grid-column: 1/2;
    color: var(--zeus);
  }
  .header__search > input::placeholder {
    color: var(--dawn);
    visibility: visible;
    font-size: 14px;
    line-height: 21px;
  }
}
.header__burger {
  padding: 0;
  border: none;
  background-color: transparent;
  font-family: inherit;
  cursor: pointer;
  width: 25px;
  height: 16px;
  position: relative;
  transition: outline 0.25s ease-in-out;
}
.header__burger::before {
  content: "";
  width: 25px;
  height: 1px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--zeus);
  transition: transform 0.25s ease-in-out, background-color 0.25s ease-in-out;
}
.header__burger::after {
  content: "";
  width: 25px;
  height: 1px;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%);
  background-color: var(--zeus);
  transition: transform 0.25s ease-in-out, background-color 0.25s ease-in-out;
}
.header__burger-decoration {
  display: block;
  width: 25px;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--zeus);
  transition: opacity 0.1s ease-in, background-color 0.25s ease-in-out;
}
.header__burger--active {
  height: 25px;
}
.header__burger--active::before, .header__burger--active::after, .header__burger--active-decoration {
  background-color: var(--white);
}
.header__burger--active::before {
  width: 1px;
  height: 35px;
  left: 100%;
  top: 35%;
  transform: rotate(-45deg) translateY(-50%);
}
.header__burger--active::after {
  width: 1px;
  height: 35px;
  left: 0;
  top: 35%;
  transform: rotate(45deg) translateY(-50%);
}
.header__burger--active > span {
  opacity: 0;
}
.header__burger:hover::before, .header__burger:hover::after, .header__burger:hover > span {
  background-color: var(--muesli-i);
}
.header__burger:focus-visible {
  outline: 1px solid var(--muesli-i);
  outline-offset: 5px;
}
.header__burger:focus-visible::before, .header__burger:focus-visible::after, .header__burger:focus-visible > span {
  background-color: var(--muesli-i);
}
@media (min-width: 1360px) {
  .header__burger {
    display: none;
  }
}
.header__logo {
  display: block;
  color: inherit;
  text-decoration: none;
  color: var(--muesli);
  width: 52px;
  height: 52px;
  justify-self: center;
  transition: color 0.25s ease-in-out;
}
.header__logo--active {
  color: var(--white);
}
.header__logo:hover {
  color: var(--muesli-i);
}
.header__logo:focus {
  color: var(--muesli-i);
  outline: 1px solid var(--muesli-i);
  outline-offset: 5px;
}
@media (min-width: 767px) {
  .header__logo {
    width: 66px;
    height: 66px;
  }
}
@media (min-width: 1360px) {
  .header__logo {
    color: var(--muesli);
  }
}
.header__user {
  justify-self: flex-end;
}
@media (min-width: 1360px) {
  .header__user {
    justify-self: auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
}
.header__cart {
  display: block;
  color: inherit;
  text-decoration: none;
  width: 18px;
  height: 16px;
}
.header__cart-link {
  display: none;
  transition: color 0.25s ease-in-out;
}
@media (min-width: 1360px) {
  .header__cart-link {
    display: initial;
    padding: 15px 0;
    font-size: 14px;
    line-height: 21px;
  }
}
.header__cart-icon {
  display: initial;
}
@media (min-width: 1360px) {
  .header__cart-icon {
    display: none;
  }
}
.header__cart-icon > path {
  transition: stroke 0.25s ease-in-out;
}
.header__cart--active > .header__cart-icon > path {
  stroke: var(--white);
}
.header__cart:hover {
  color: var(--muesli-i);
}
.header__cart:hover > .header__cart-icon > path {
  stroke: var(--muesli-i);
}
.header__cart:focus-visible {
  color: var(--muesli-i);
  outline: 1px solid var(--muesli-i);
  outline-offset: 5px;
}
.header__cart:focus-visible .header__cart-icon > path {
  stroke: var(--muesli-i);
}
@media (min-width: 1360px) {
  .header__cart {
    width: initial;
    height: initial;
  }
}
.header__info {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
  display: none;
}
.header__info--active {
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-start;
}
@media (min-width: 1360px) {
  .header__info {
    display: none;
  }
}
.header__info-item:not(:last-child) {
  margin-bottom: 30px;
}
.header__info-link {
  display: block;
  color: inherit;
  text-decoration: none;
  position: relative;
  font-size: 18px;
  color: var(--white);
  opacity: 0.75;
  transition: opacity 0.25s ease-in-out;
}
.header__info-link::after {
  content: "";
  display: block;
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 0;
  border-bottom: 2px dashed var(--muesli-i);
  transition: width 0.35s ease-in-out;
}
.header__info-link:hover {
  opacity: 1;
}
.header__info-link:hover::after {
  width: 100%;
}
.header__info-link:focus-visible {
  opacity: 1;
  outline: 2px solid var(--muesli-i);
  outline-offset: 5px;
}
.header__login {
  padding: 0;
  border: none;
  background-color: transparent;
  font-family: inherit;
  cursor: pointer;
  justify-self: flex-start;
  display: none;
  width: 50px;
  height: 25px;
  position: absolute;
  bottom: 35px;
  left: 15px;
  font-size: 18px;
  line-height: 24px;
  color: var(--white);
  transition: text-shadow 0.25s ease-in-out;
}
.header__login--active {
  display: initial;
}
.header__login:hover {
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.header__login:focus-visible {
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  outline: 1px solid var(--muesli-i);
  outline-offset: 3px;
}
@media (min-width: 767px) {
  .header__login {
    left: 45px;
    bottom: 50px;
  }
}
@media (min-width: 1360px) {
  .header__login {
    position: initial;
    display: initial;
    margin-right: 35px;
    font-size: 14px;
    line-height: 21px;
    color: var(--zeus);
    order: -1;
  }
}

.search {
  position: relative;
}
.search__input {
  padding: 0;
  border: 0;
  font-family: inherit;
  font-weight: 500;
  font-size: 14px;
  min-width: 100%;
  padding: 12px 12px 12px 40px;
  background-color: var(--search-bg);
  border-radius: 21px;
  transition: box-shadow 0.25s ease-in-out, outline 0.25s ease-in-out;
}
.search__input:hover {
  box-shadow: 3px 2px 1px 0 var(--muesli-i);
}
.search__input:focus-visible {
  box-shadow: 3px 2px 1px 0 var(--muesli-i);
  outline: 1px solid var(--muesli-i);
  outline-offset: 3px;
}
.search__button {
  padding: 0;
  border: none;
  background-color: transparent;
  font-family: inherit;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
  color: var(--white);
}
@media (min-width: 1360px) {
  .search__button {
    color: var(--zeus);
  }
}
.search__button:hover {
  color: var(--muesli-i);
}
.search__button:focus-visible {
  color: var(--muesli-i);
  outline: 1px solid var(--muesli-i);
  outline-offset: 3px;
}

.nav {
  display: none;
  color: var(--zeus);
}
.nav--active {
  display: initial;
  color: var(--white);
}
@media (min-width: 1360px) {
  .nav {
    display: initial;
    color: var(--zeus);
    border-top: 1px solid var(--desert-storm);
  }
}
.nav__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-start;
}
@media (min-width: 1360px) {
  .nav__list {
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
  }
}
.nav__item:not(:last-child) {
  margin-bottom: 40px;
}
@media (min-width: 1360px) {
  .nav__item:not(:last-child) {
    margin-bottom: 0;
    margin-right: 35px;
  }
}
.nav__link {
  display: block;
  color: inherit;
  text-decoration: none;
  position: relative;
  font-size: 24px;
  line-height: 36px;
  transition: text-shadow 0.25s ease-in-out, outline 0.25s ease-in-out;
}
@media (min-width: 1360px) {
  .nav__link {
    font-size: 16px;
    padding: 18px 0;
  }
}
.nav__link::after {
  content: "";
  display: block;
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 0;
  height: 0;
  border-bottom: 1px solid var(--muesli-i);
  transition: width 0.35s ease-in-out;
}
@media (min-width: 1360px) {
  .nav__link::after {
    bottom: 16px;
    left: 50%;
    transform: translateX(-50%);
    border-color: var(--muesli);
  }
}
.nav__link:hover {
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.nav__link:hover::after {
  width: 100%;
}
.nav__link:focus-visible {
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  outline: 2px solid var(--muesli-i);
  outline-offset: 5px;
}
@media (min-width: 1360px) {
  .nav__link:focus-visible {
    outline-width: 1px;
    outline-offset: 0;
  }
}

.main {
  flex: 1;
  display: grid;
  row-gap: 70px;
}
@media (min-width: 767px) {
  .main {
    row-gap: 80px;
  }
}
@media (min-width: 1360px) {
  .main {
    row-gap: 130px;
  }
}

.hero {
  position: relative;
  background-color: var(--muesli);
  background-image: url(../images/hero/hero-bg.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .hero {
    background-image: url(../images/hero/hero-bg@2x.webp);
  }
}
@media (min-width: 767px) {
  .hero {
    background-image: url(../images/hero/hero-bg--tablet.webp);
  }
}
@media (min-width: 767px) and (min-resolution: 2dppx) {
  .hero {
    background-image: url(../images/hero/hero-bg--tablet@2x.webp);
  }
}
@media (min-width: 1360px) {
  .hero {
    background-image: url(../images/hero/hero-bg--desktop.webp);
  }
}
@media (min-width: 1360px) and (min-resolution: 2dppx) {
  .hero {
    background-image: url(../images/hero/hero-bg--desktop@2x.webp);
  }
}
.hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--hero-bg);
}
.hero__about {
  text-align: center;
  z-index: 2;
}
.hero__container {
  min-height: 460px;
  padding: 0 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
@media (min-width: 767px) {
  .hero__container {
    min-height: 465px;
    padding: 0 45px;
  }
}
@media (min-width: 1360px) {
  .hero__container {
    padding: 0 95px;
  }
}
.hero__title {
  margin: 0;
  margin-bottom: 15px;
  font-family: "Garamond";
  font-style: normal;
  font-weight: 700;
  font-size: 38px;
  line-height: 50px;
  color: var(--white);
}
@media (min-width: 767px) {
  .hero__title {
    font-size: 45px;
    line-height: 58px;
  }
}
@media (min-width: 767px) {
  .hero__title {
    margin-bottom: 10px;
  }
}
.hero__description {
  margin-top: 0;
  margin-bottom: 0;
  font-family: "Garamond";
  font-style: italic;
  font-weight: 400;
  font-size: 34px;
  color: var(--white);
}
@media (min-width: 767px) {
  .hero__description {
    font-size: 38px;
  }
}

.new {
  background-color: var(--white);
}
.new__container {
  padding: 0 15px;
}
@media (min-width: 767px) {
  .new__container {
    padding: 0 45px;
    padding-bottom: 60px;
  }
}
@media (min-width: 1360px) {
  .new__container {
    padding: 0 95px;
    padding-bottom: 0;
  }
}
.new__header {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}
.new__header-description {
  text-align: center;
}
@media (min-width: 767px) {
  .new__header {
    justify-content: space-between;
    margin-bottom: 60px;
  }
}
.new__title {
  margin-bottom: 35px;
}
.new__button {
  padding: 0;
  border: none;
  background-color: transparent;
  font-family: inherit;
  cursor: pointer;
  display: none;
  width: 36px;
  height: 15px;
  transition: outline 0.25s ease-in-out;
}
@media (min-width: 767px) {
  .new__button {
    display: initial;
  }
}
.new__button:hover .new__button-arrow {
  transform: translateX(-10px);
}
.new__button:hover .new__button-arrow--reversed {
  transform: translateX(10px) rotate(180deg);
}
.new__button:focus-visible {
  outline: 1px solid var(--muesli-i);
  outline-offset: 3px;
}
.new__button-arrow {
  transition: transform 0.25s ease-in-out;
}
.new__button-arrow--reversed {
  transform: rotate(180deg);
}

.slider {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 30px;
}
.slider__item:nth-child(3) {
  display: none;
}
@media (min-width: 465px) {
  .slider__item:nth-child(3) {
    display: initial;
  }
}
@media (min-width: 767px) {
  .slider__item:nth-child(3) {
    display: none;
  }
}
@media (min-width: 1120px) {
  .slider__item:nth-child(3) {
    display: initial;
  }
}
.slider__item:nth-child(4) {
  display: none;
}
@media (min-width: 1360px) {
  .slider__item:nth-child(4) {
    display: initial;
  }
}
.slider__link {
  display: block;
  color: inherit;
  text-decoration: none;
  transition: outline 0.25s ease-in-out;
}
.slider__link:hover .slider__title {
  text-shadow: var(--text-d);
}
.slider__link:focus {
  outline: 1px solid var(--muesli-i);
  outline-offset: 3px;
}
.slider__article {
  text-align: center;
}
.slider__figure {
  margin: 0;
  margin-bottom: 15px;
}
.slider__image {
  margin-bottom: 20px;
}
@media (min-width: 767px) {
  .slider__image {
    width: 324px;
    height: 340px;
    margin-bottom: 25px;
  }
}
@media (min-width: 1360px) {
  .slider__image {
    width: 270px;
    height: 284px;
  }
}
.slider__title {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
  max-width: 130px;
  transition: text-shadow 0.25s ease-in-out;
}
@media (min-width: 767px) {
  .slider__title {
    max-width: 324px;
  }
}
.slider__price {
  font-size: 14px;
