}
.question__item {
  border: 1px solid var(--muesli);
  padding: 10px 60px 10px 10px;
  position: relative;
}
.question__item:not(:first-child) {
  border-top: none;
}
@media (min-width: 767px) {
  .question__item {
    padding: 25px 100px 25px 25px;
  }
}
.question__label {
  display: block;
  padding: 15px 0 15px 0;
  position: relative;
  line-height: 140%;
  cursor: pointer;
  transition: text-shadow 0.25s ease-in-out;
}
.question__label::after {
  content: "";
  width: 0;
  position: absolute;
  left: 0;
  bottom: 5px;
  border-bottom: 2px dashed var(--muesli);
  transition: width 0.25s ease-in-out;
}
@media (min-width: 767px) {
  .question__label {
    font-size: 18px;
    padding: 25px 0 25px 0;
  }
}
.question__label:hover {
  text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
}
.question__label:hover + .question__decoration {
  top: 23px;
  border-color: var(--muesli-i);
}
@media (min-width: 767px) {
  .question__label:hover + .question__decoration {
    top: 47px;
  }
}
.question__decoration {
  display: block;
  width: 15px;
  height: 15px;
  position: absolute;
  top: 20px;
  right: 25px;
  transform: rotate(135deg);
  border-top: 2px solid var(--muesli);
  border-right: 2px solid var(--muesli);
  transition: transform 0.35s ease-in-out, top 0.25s ease-in-out, border-color 0.25s ease-out;
}
@media (min-width: 767px) {
  .question__decoration {
    top: 45px;
    right: 50px;
  }
}
.question__answer {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  display: none;
}
@media (min-width: 767px) {
  .question__answer {
    font-size: 16px;
  }
}
.question__input:focus-visible + .question__label {
  text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
}
.question__input:focus-visible + .question__label::after {
  width: 100%;
}
.question__input:checked + .question__label {
  margin-bottom: 10px;
}
.question__input:checked ~ .question__decoration {
  top: 30px;
  transform: rotate(315deg);
  transition: transform 0.25s ease-in-out;
}
@media (min-width: 767px) {
  .question__input:checked ~ .question__decoration {
    top: 50px;
  }
}
.question__input:checked ~ .question__answer {
  display: block;
}

