.inbox {
  &__title {
    @include inbox-title;
    margin-top: 0;
    margin-bottom: 30px;
  }

  &__description {
    margin-top: 0;
    margin-bottom: 50px;
    line-height: 140%;
    color: var(--dawn);
  }

  &__email {
    position: relative;
  }

  &__input {
    @include reset-input;
    min-width: 100%;
    padding: 15px 110px 15px 10px;
    background-color: transparent;
    border-bottom: 1px solid var(--white);
    font-size: 14px;
    line-height: 21px;
    color: var(--dawn);
    transition: 
    outline 0.25s ease-in-out, 
    box-shadow 0.25s ease-in-out;

    &::placeholder {
      color: var(--dawn);
    }

    &:hover {
      outline: 1px solid var(--white);
      box-shadow: 2px 4px 1px 0 var(--white);
    }

    &:focus-visible {
      outline: 1px solid var(--white);
    }
  }

  &__button {
    @include reset-button;
    min-width: 100px;
    padding: 15px 0;
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    gap: 12px;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    color: var(--white);
    transition: opacity 0.25s ease-in-out, outline 0.25s ease-in-out;

    &:hover {
      opacity: 0.7;

      & .inbox__icon {
        transform: rotate(180deg) translateX(-5px);
      }
    }

    &:focus-visible {
      outline: 1px solid var(--white);
      outline-offset: 1px;
    }
  }

  &__icon {
    width: 29px;
    height: 9px;
    transform: rotate(180deg);
    transition: transform 0.25s ease-in-out;
  }
}
