.nav {
  display: none;
  color: var(--zeus);

  &--active {
    display: initial;
    color: var(--white);
  }

  @include media($desktop) {
    display: initial;
    color: var(--zeus);
    border-top: 1px solid var(--desert-storm);
  }

  &__list {
    @include reset-list;
    display: flex;
    flex-flow: column nowrap;
    align-items: flex-start;

    @include media($desktop) {
      flex-flow: row wrap;
      justify-content: center;
      align-items: center;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 40px;

      @include media($desktop) {
        margin-bottom: 0;
        margin-right: 35px;
      }
    }
  }

  &__link {
    @include reset-link;
    position: relative;
    font-size: 24px;
    line-height: 36px;
    transition: text-shadow 0.25s ease-in-out, outline 0.25s ease-in-out;
    
    @include media($desktop) {
      font-size: 16px;
      padding: 18px 0;
    }

    &::after {
      content: "";
      display: block;
      position: absolute;
      bottom: -5px;
      left: 0;
      width: 0;
      height: 0;
      border-bottom: 1px solid var(--muesli-i);
      transition: width 0.35s ease-in-out;

      @include media($desktop) {
        bottom: 16px;
        left: 50%;
        transform: translateX(-50%);
        border-color: var(--muesli);
      }
    }

    &:hover {
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

      &::after {
        width: 100%;
      }
    }

    &:focus-visible {
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      outline: 2px solid var(--muesli-i);
      outline-offset: 5px;

      @include media($desktop) {
        outline-width: 1px;
        outline-offset: 0;
      }
    }
  }
}
