// Flex
@mixin flex-b-s {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

@mixin flex-b-c {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@mixin flex-b-e {
  display: flex;
  justify-content: space-between;
  align-items: end;
}

@mixin flex-s-c {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

@mixin flex-all-c {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin flex-e-c {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

@mixin flex-c-s {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}


// Resets
@mixin reset-button {
  padding: 0;
  border: none;
  background-color: transparent;
  font-family: inherit;
  cursor: pointer;
}

@mixin reset-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}

@mixin reset-link {
  display: block;
  color: inherit;
  text-decoration: none;
}

@mixin reset-input {
  padding: 0;
  border: 0;
  font-family: inherit;
}

@mixin reset-fieldset {
  padding: 0;
  border: none;
}

@mixin reset-figure {
  margin: 0;
}

// Fonts

@mixin hero-title {
  font-family: "Garamond";
  font-style: normal;
  font-weight: 700;
  font-size: 38px;
  line-height: 50px;

  @include media($tablet) {
    font-size: 45px;
    line-height: 58px;
  }
}

@mixin hero-description {
  font-family: "Garamond";
  font-style: italic;
  font-weight: 400;
  font-size: 34px;

  @include media($tablet) {
    font-size: 38px;
  }
}

@mixin article-title {
  font-family: "Garamond";
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  line-height: 33px;
}

@mixin description-font {
  font-weight: 500;
  font-size: 14px;
}

@mixin inbox-title {
  font-family: "Garamond";
  font-style: normal;
  font-weight: 700;
  font-size: 34px;
  letter-spacing: 0.03em;
  line-height: 130%;
  color: var(--white);
}

// Media
@mixin media($width) {
  @media (min-width: $width) {
    @content;
  }
}

@mixin burger {
  &::before {
    content: "";
    width: 25px;
    height: 1px;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--zeus);
    transition: transform 0.25s ease-in-out, background-color 0.25s ease-in-out;
  }

  &::after {
    content: "";
    width: 25px;
    height: 1px;
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translate(-50%);
    background-color: var(--zeus);
    transition: transform 0.25s ease-in-out, background-color 0.25s ease-in-out;
  }

  &-decoration {
    display: block;
    width: 25px;
    height: 1px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: var(--zeus);
    transition: opacity 0.1s ease-in, background-color 0.25s ease-in-out;
  }
}

@mixin burger-active {
  &::before {
    width: 1px;
    height: 35px;
    left: 100%;
    top: 35%;
    transform: rotate(-45deg) translateY(-50%);
  }

  &::after {
    width: 1px;
    height: 35px;
    left: 0;
    top: 35%;
    transform: rotate(45deg) translateY(-50%);
  }

  & > span {
    opacity: 0;
  }
}
