.header {
  background-color: var(--white);

  &--active {
    background-color: var(--muesli);
    overflow: hidden;
  }

  @include media($desktop) {
    background-color: var(--white);
  }

  &__container {
    position: relative;
    padding: 20px 15px;
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 45px;

    @include media($tablet) {
      row-gap: 55px;
      padding: 30px 45px;
    }

    &--active {
      padding-bottom: 100px;

      @include media($tablet) {
        padding-bottom: 130px;
      }
    }

    @include media($desktop) {
      row-gap: 35px;
      padding: 30px 95px;
      padding-bottom: 0;
    }
  }

  &__menu {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    justify-content: space-between;
    row-gap: 30px;

    @include media($tablet) {
      row-gap: 25px;
    }

    @include media($desktop) {
      row-gap: 0;
      column-gap: 350px;
      grid-template-columns: 1fr 66px 1fr;
    }
  }

  &__search {
    display: none;
    grid-column: 1 / -1;

    &--active {
      display: initial;
      color: var(--white);
      grid-row: 2 / 3;

      > input::placeholder {
        visibility: hidden;
      }
    }

    @include media($desktop) {
      display: initial;
      grid-row: 1 / 2;
      grid-column: 1 / 2;
      color: var(--zeus);

      > input::placeholder {
        color: var(--dawn);
        visibility: visible;
        font-size: 14px;
        line-height: 21px;
      }
    }
  }

  &__burger {
    @include reset-button;
    @include burger;
    width: 25px;
    height: 16px;
    position: relative;
    transition: outline 0.25s ease-in-out;

    &--active {
      height: 25px;

      &::before,
      &::after,
      &-decoration {
        background-color: var(--white);
      }

      @include burger-active;
    }

    &:hover {
      &::before,
      &::after,
      & > span {
        background-color: var(--muesli-i);
      }
    }

    &:focus-visible {
      outline: 1px solid var(--muesli-i);
      outline-offset: 5px;
    }

    &:focus-visible {
      &::before,
      &::after,
      & > span {
        background-color: var(--muesli-i);
      }
    }

    @include media($desktop) {
      display: none;
    }
  }

  &__logo {
    @include reset-link;
    color: var(--muesli);
    width: 52px;
    height: 52px;
    justify-self: center;
    transition: color 0.25s ease-in-out;

    &--active {
      color: var(--white);
    }

    &:hover {
      color: var(--muesli-i);
    }

    &:focus {
      color: var(--muesli-i);
      outline: 1px solid var(--muesli-i);
      outline-offset: 5px;
    }

    @include media($tablet) {
      width: 66px;
      height: 66px;
    }

    @include media($desktop) {
      color: var(--muesli);
    }
  }

  &__user {
    justify-self: flex-end;

    @include media($desktop) {
      justify-self: auto;
      @include flex-e-c;
    }
  }

  &__cart {
    @include reset-link;
    width: 18px;
    height: 16px;

    &-link {
      display: none;
      transition: color 0.25s ease-in-out;

      @include media($desktop) {
        display: initial;
        padding: 15px 0;
        font-size: 14px;
        line-height: 21px;
      }
    }

    &-icon {
      display: initial;

      @include media($desktop) {
        display: none;
      }

      > path {
        transition: stroke 0.25s ease-in-out;
      }
    }

    &--active > &-icon > path {
      stroke: var(--white);
    }

    &:hover {
      color: var(--muesli-i);
    }

    &:hover > &-icon > path {
      stroke: var(--muesli-i);
    }

    &:focus-visible {
      color: var(--muesli-i);
      outline: 1px solid var(--muesli-i);
      outline-offset: 5px;
    }

    &:focus-visible &-icon > path {
      stroke: var(--muesli-i);
    }

    @include media($desktop) {
      width: initial;
      height: initial;
    }
  }

  &__info {
    @include reset-list;
    display: none;

    &--active {
      display: flex;
      flex-flow: column nowrap;
      align-items: flex-start;
    }

    @include media($desktop) {
      display: none;
    }

    &-item {
      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }

    &-link {
      @include reset-link;
      position: relative;
      font-size: 18px;
      color: var(--white);
      opacity: 0.75;
      transition: opacity 0.25s ease-in-out;

      &::after {
        content: "";
        display: block;
        position: absolute;
        bottom: -5px;
        left: 0;
        width: 0;
        height: 0;
        border-bottom: 2px dashed var(--muesli-i);
        transition: width 0.35s ease-in-out;
      }

      &:hover {
        opacity: 1;

        &::after {
          width: 100%;
        }
      }

      &:focus-visible {
        opacity: 1;
        outline: 2px solid var(--muesli-i);
        outline-offset: 5px;
      }
    }
  }

  &__login {
    @include reset-button;
    justify-self: flex-start;
    display: none;
    width: 50px;
    height: 25px;
    position: absolute;
    bottom: 35px;
    left: 15px;
    font-size: 18px;
    line-height: 24px;
    color: var(--white);
    transition: text-shadow 0.25s ease-in-out;

    &--active {
      display: initial;
    }

    &:hover {
      text-shadow: 0px 4px 4px rgb(0 0 0 / 25%);
    }

    &:focus-visible {
      text-shadow: 0px 4px 4px rgb(0 0 0 / 25%);
      outline: 1px solid var(--muesli-i);
      outline-offset: 3px;
    }

    @include media($tablet) {
      left: 45px;
      bottom: 50px;
    }

    @include media($desktop) {
      position: initial;
      display: initial;
      margin-right: 35px;
      font-size: 14px;
      line-height: 21px;
      color: var(--zeus);
      order: -1;
    }
  }
}
