.blog {
  &__container {
    padding: 0 15px;

    @include media($tablet) {
      padding: 0 45px;
    }

    @include media($desktop) {
      padding: 0 95px;
    }
  }

  &__header {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    margin-bottom: 60px;

    @include media($tablet) {
      margin-bottom: 70px;
    }
  }

  &__title {
    margin-bottom: 35px;
  }

  &__list {
    @include reset-list;
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    row-gap: 40px;

    @include media($tablet) {
      row-gap: 50px;
    }

    @include media($desktop) {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(2, 300px);
      gap: 30px;
    }
  }

  &__item {
    @include media($tablet) {
      &:nth-child(2n + 2) {
        & picture {
          grid-column: 2 / -1;
          grid-row: 1 / 2;
        }
      }
    }

    @include media($desktop) {
      &:nth-child(3n + 3) {
        grid-column: 2 / -1;
        grid-row: 1 / -1;
        position: relative;

        & .article {
          @include media($desktop) {
            grid-template-columns: 1fr;
          }
        }

        & .article__image {
          @include media($desktop) {
            width: 570px;
            height: 630px;
          }
        }

        & .article__about {
          position: absolute;
          bottom: 0;
          right: 0;
          width: 285px;
          min-height: 300px;
        }
      }
    }
  }
}
