.hero {
  position: relative;
  background-color: var(--muesli);
  background-image: url(../images/hero/hero-bg.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-resolution: 2dppx) {
    background-image: url(../images/hero/hero-bg@2x.webp);
  }

  @include media($tablet) {
    background-image: url(../images/hero/hero-bg--tablet.webp);

    @media (min-resolution: 2dppx) {
      background-image: url(../images/hero/hero-bg--tablet@2x.webp);
    }
  }

  @include media($desktop) {
    background-image: url(../images/hero/hero-bg--desktop.webp);

    @media (min-resolution: 2dppx) {
      background-image: url(../images/hero/hero-bg--desktop@2x.webp);
    }
  }


  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--hero-bg);
  }

  &__about {
    text-align: center;
    z-index: 2;
  }

  &__container {
    min-height: 460px;
    padding: 0 15px;
    @include flex-all-c;
    flex-wrap: wrap;

    @include media($tablet) {
      min-height: 465px;
      padding: 0 45px;
    }

    @include media($desktop) {
      padding: 0 95px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 15px;
    @include hero-title;
    color: var(--white);

    @include media($tablet) {
      margin-bottom: 10px;
    }
  }

  &__description {
    margin-top: 0;
    margin-bottom: 0;
    @include hero-description;
    color: var(--white);
  }
}