.follow {
  &__container {
    padding: 0 15px;
    padding-bottom: 70px;

    @include media($tablet) {
      padding: 0 45px;
      padding-bottom: 80px;
    }

    @include media($desktop) {
      padding: 0 95px;
      padding-bottom: 130px;
    }
  }

  &__header {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    text-align: center;
    margin-bottom: 60px;

    @include media($tablet) {
    margin-bottom: 70px;
    }
  }

  &__title {
    margin-bottom: 35px;
  }

  &__link {
    @include reset-link;
    transition: outline 0.25s ease-in-out;

    &:focus-visible {
      outline: 1px solid var(--muesli);
      outline-offset: 2px;
    }
  }

  &__image {
    min-width: 100%;
    height: 100%;
  }
  
  &__posts {
    @include reset-list;
    display: grid;
    grid-template-columns: repeat(2, 133px);
    grid-template-rows: 133px repeat(2, 87px);
    justify-content: center;
    gap: 27px 24px;

    @include media($tablet) {
      grid-template-columns: 133px 231px repeat(2, 133px);
      grid-template-rows: 133px 87px;
      gap: 21px 16px;
    }

    @include media($desktop) {
      grid-template-columns: 230px 400px repeat(2, 230px);
      grid-template-rows: 230px 150px;
      gap: 34px 27px;
    }
  }

  &__post {
    &:nth-child(3) {
      grid-column: 2 / -1;
      grid-row: 1 / 3;

      @include media($tablet) {
        grid-column: 2 / 3;
        grid-row: 1 / -1;
      }
    }

    &:nth-child(6) {
      display: none;

      @include media($tablet) {
        display: initial;
        grid-column: 3 / 4;
        grid-row: 1 / 2;
      }
    }

    &:last-child {
      display: none;

      @include media($tablet) {
        display: initial;
        grid-column: 4 / -1;
        grid-row: 1 / 2;
      }
    }
  }
}