// Css variables
:root {
  // Colors
  --white: #ffffff;
  --zeus: #1d1613;
  --muesli: #a87b62;
  --muesli-i: #7a5340;
  --dawn: #9b9997;
  --desert-storm: #f6f5f4;
  --judge-gray: #564034;
  --search-bg: rgba(255, 255, 255, 0.15);
  --hero-bg: rgba(86, 64, 52, 0.7);
  --article-bg: #f0eae4;
  // Fonts
  --head-f: 'Poppins', 'Arial', sans-serif;
  --head-f-w: 500;
  --head-f-s: 16px;
  --head-l-h: 130%;
  --head-f-style: normal;
  // Decorations
  --text-d: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

