.link {
  display: block;
  color: inherit;
  text-decoration: none;
  position: relative;
  transition: text-shadow 0.25s ease-in-out, outline 0.25s ease-in-out;

  &:focus-visible {
    outline: 1px solid var(--muesli);
    outline-offset: 2px;
  }

  &::after {
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -10px;
    width: 100%;
    height: 1px;
    background-color: var(--muesli);
    transition: width 0.25s ease-in-out;
  }

  &:hover {
    text-shadow: 0px 4px 4px rgb(0 0 0 / 25%);
    
    &::after {
      width: 0;
    }
  }
}
