*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

img {
  display: block;
  max-width: 100%;
  object-fit: contain;
}

svg {
  display: block;
  height: 100%;
  width: 100%;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  font-family: var(--head-f);
  font-weight: var(--head-f-w);
  font-size: var(--head-f-s);
  font-style: var(--head-f-style);
  line-height: var(--head-l-h);
}