.article {
  display: grid;
  grid-template-columns: 1fr;

  @include media($tablet) {
    grid-template-columns: repeat(2, 1fr);
  }

  &__image {
    object-fit: cover;
    min-width: 100%;

    @include media($tablet) {
      height: 300px;
    }
  }

  &__about {
    padding: 55px 0;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    text-align: center;
    background-color: var(--article-bg);
  }

  &__time {
    margin-bottom: 25px;
    font-size: 14px;
    color: var(--dawn);
  }

  &__title {
    @include article-title;
    margin-top: 0;
    margin-bottom: 50px;

    @include media($tablet) {
      max-width: 165px;
    }
  }
}