.question {

  &__container {
    padding: 0 15px;

    @include media($tablet) {
      padding: 0 45px;
    }

    @include media($desktop) {
      padding: 0 195px;
    }
  }

  &__title {
    text-align: center;
    margin-bottom: 40px;

    @include media($tablet) {
      margin-bottom: 60px;
    }
  }

  &__list {
    @include reset-list;
  }

  &__item {
    border: 1px solid var(--muesli);
    padding: 10px 60px 10px 10px;
    position: relative;

    &:not(:first-child) {
      border-top: none;
    }

    @include media($tablet) {
      padding: 25px 100px 25px 25px;
    }
  }

  &__label {
    display: block;
    padding: 15px 0 15px 0;
    position: relative;
    line-height: 140%;
    cursor: pointer;
    transition: text-shadow 0.25s ease-in-out;

    &::after {
      content: "";
      width: 0;
      position: absolute;
      left: 0;
      bottom: 5px;
      border-bottom: 2px dashed var(--muesli);
      transition: width 0.25s ease-in-out;
    }

    @include media($tablet) {
      font-size: 18px;
      padding: 25px 0 25px 0;
    }

    &:hover {
      text-shadow: 0px 2px 2px rgb(0 0 0 / 25%);
    }

    &:hover + .question__decoration {
      top: 23px;
      border-color: var(--muesli-i);

      @include media($tablet) {
        top: 47px;
      }
    }
  }

  &__decoration {
    display: block;
    width: 15px;
    height: 15px;
    position: absolute;
    top: 20px;
    right: 25px;
    transform: rotate(135deg);
    border-top: 2px solid var(--muesli);
    border-right: 2px solid var(--muesli);
    transition: transform 0.35s ease-in-out, top 0.25s ease-in-out, border-color 0.25s ease-out;

    @include media($tablet) {
      top: 45px;
      right: 50px;
    }
  }

  &__answer {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    display: none;

    @include media($tablet) {
      font-size: 16px;
    }
  }

  &__input {
    &:focus-visible + .question__label{
      text-shadow: 0px 2px 2px rgb(0 0 0 / 25%);

      &::after {
        width: 100%;
      }
    }

    &:checked + .question__label {
      margin-bottom: 10px;
    }

    &:checked ~ .question__decoration {
      top: 30px;
      transform: rotate(315deg);
      transition: transform 0.25s ease-in-out;

      @include media($tablet) {
        top: 50px;
      }
    }

    &:checked ~ .question__answer {
      display: block;
    }
  }
}