.social {
  @include reset-list;
  display: flex;
  align-items: center;

  @include media($tablet) {
    min-width: 100%;
    flex-flow: column nowrap;
    align-items: flex-start;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 30px;

      @include media($tablet) {
        margin-right: 0;
      }
    }
  }

  &__link {
    @include reset-link;
    color: var(--white);
    transition: opacity 0.25s ease-in-out, outline 0.25s ease-in-out;

    &:hover {
      opacity: 0.75;
    }

    &:focus-visible {
      outline: 1px solid var(--white);
      outline-offset: 3px;
      opacity: 0.75;
    }

    @include media($tablet) {
      @include flex-s-c;
      padding: 15px 0;
    }
  }

  &__icon {
    width: 20px;
    height: 21px;

    @include media($tablet) {
      margin-right: 15px;
    }
  }

  &__name {
    display: none;

    @include media($tablet) {
      display: initial;
    }
  }
}
