.new {
  background-color: var(--white);

  &__container {
    padding: 0 15px;

    @include media($tablet) {
      padding: 0 45px;
      padding-bottom: 60px;
    }

    @include media($desktop) {
      padding: 0 95px;
      padding-bottom: 0;
    }
  }

  &__header {
    @include flex-all-c;
    margin-bottom: 50px;

    &-description {
      text-align: center;
    }

    @include media($tablet) {
      justify-content: space-between;
      margin-bottom: 60px;
    }
  }

  &__title {
    margin-bottom: 35px;
  }

  &__button {
    @include reset-button;
    display: none;
    width: 36px;
    height: 15px;
    transition: outline 0.25s ease-in-out;

    @include media($tablet) {
      display: initial;
    }

    &:hover &-arrow {
      transform: translateX(-10px);
    }

    &:hover &-arrow--reversed {
      transform: translateX(10px) rotate(180deg);
    }

    &:focus-visible {
      outline: 1px solid var(--muesli-i);
      outline-offset: 3px;
    }

    &-arrow {
      transition: transform 0.25s ease-in-out;

      &--reversed {
        transform: rotate(180deg);
      }
    }
  }
}